import { useEffect, useContext } from "react";
import { isHasIdentifierInStorage } from "helpers/auth.helper";
import { AppContext } from "contexts/app.context";
import { getMyData } from "services/user.service";

const AuthLayout = ({ children }) => {
  const { isAuth, setAuth, setLoading, setUser } = useContext(AppContext);
  const identifier = isHasIdentifierInStorage();
  useEffect(() => {
    console.log('useEffect in')
    const checkAuth = async () => {
      console.log('checkAuth',identifier, isAuth)
      setLoading(true);
      if (identifier && !isAuth) {
        const { success, userData } = await getMyData();
        if (success) {
          setUser(userData);
          setAuth(true);
        }
      }
      setLoading(false);
    };
    checkAuth();
  }, [identifier, isAuth ]);

  return <div>{children}</div>;
};

export default AuthLayout;
